.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.clearbutton{
  border-radius: 8px;
  padding: 8px 13px;
  background-color: white;
}
.clearbutton:hover{
 background-color: black;
color: white;
}

.search{
  padding: 8px 13px;
  margin: 0 5px;  
  border-radius: 8px;
  color: green;
  background:transparent;
}
.search:hover{
   background-color: green;
color: white;
}
.message{
  background-color: #fff3cd;
  width: 50%;
  height: 100px;
  padding: 4%;
  padding-bottom: 7%;
margin:6% auto;
font-size:14px;
}